import Vue from 'vue'
import VueRouter from 'vue-router'
// import Home from '../views/Home.vue'
import Layout from '../views/Layout.vue'
import a from '../views/a.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/a',
    component: a
  },
  {
    path: '/',
    redirect: '/login'
  },
  {
    path: '/home',
    component: Layout,
    children: [
      {
        path: '',
        component: () => import('@/views/Home.vue')
      },
      {
        path: '/practice',
        component: () => import('@/views/Practice')
      },
      {
        path: '/task',
        component: () => import('@/views/Task')
      },
      {
        path: '/statistics',
        component: () => import('@/views/statistics')
      }
    ]
  },
  {
    path: '/login',
    component: () => import('@/views/Login')
  },
  {
    path: '/students/home',
    component: () => import('@/views/Students/Home'),
    redirect: '/students/practiceList',
    children: [
      {
        path: '/students/practiceList',
        component: () => import('@/views/Students/PracticeList')
      },
      {
        path: '/students/taskList',
        component: () => import('@/views/Students/TaskList')
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

