import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    classId: localStorage.getItem('classId') || '',
    isLoading: false
  },
  mutations: {
    setClassId (state, id) {
      state.classId = id
      localStorage.setItem('classId', id)
    },
    setLoading(state, flag) {
      state.isLoading = flag
    }
  },
  actions: {
  },
  modules: {
  }
})
