<template>
  <div>
    <div class="header">
      <p>hi~ 老师</p>
      <el-menu text-color="#000" active-text-color="#C00000" :default-active="activeIndex" class="el-menu-demo" mode="horizontal" router>
        <el-menu-item index="/home">首页</el-menu-item>
        <el-menu-item index="/practice">练习</el-menu-item>
        <el-menu-item index="/task">作业</el-menu-item>
        <el-menu-item index="/statistics">统计</el-menu-item>
      </el-menu>
      <div>
        <el-select v-model="value" placeholder="请选择练习时间/分钟">
          <el-option
            v-for="(item, i) in options"
            :key="i"
            :label="item"
            :value="item">
          </el-option>
        </el-select>
        <el-button style="margin: 0 10px;" @click="startTask">开始</el-button>
        <span style="width: 200px; display:inline-block;" v-if="countDown > 0">距离练习结束还剩：{{ countDown }} 秒</span>
        <span style="width: 200px; display:inline-block;" v-else>练习未开始或已结束</span>
      </div>
      <el-button @click="logout">退出</el-button>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeIndex: this.$route.path,
      options: [1, 2, 3, 4, 5, 10, 12, 15, 20],
      value: '',
      countDown: 0
    }
  },

  created() {
    if (localStorage.getItem('countDown')) {
      const date = localStorage.getItem('countDown')
      const endTime = new Date(date)
      this.countDown = Math.floor((endTime - new Date()) / 1000)

      this.id = setInterval(() => {
        this.countDown--
        if (this.countDown < 0) {
          clearInterval(this.id)
          localStorage.removeItem('countDown', date)
        }
      }, 1000)
    }
  },

  methods: {
    logout() {
      this.$store.commit('setClassId', null)
      localStorage.removeItem('hmhz-intergral-token')
      localStorage.removeItem('classId')
      this.$router.push('/login')
    },
    startTask() {
      if (this.id) {
        clearInterval(this.id)
      }
      const date = new Date(Date.now() + (this.value * 60 * 1000))
      localStorage.setItem('countDown', date)
      this.countDown = this.value * 60
      this.id = setInterval(() => {
        this.countDown--
        if (this.countDown < 0) {
          clearInterval(this.id)
          localStorage.removeItem('countDown', date)
        }
      }, 1000)
    }
  }
}
</script>

<style scoped lang='less'>
.header {
  display: flex;
  justify-content: space-between;
  padding: 0 15px;
  align-items: center;
  background-color: #e3e6ea;
  .start_btn {
    background-color: #c00000;
  }
  .el-menu {
    background-color: transparent;
    .el-menu-item:hover,
    .el-menu-item:focus {
      background-color: transparent;
    }
  }
}
</style>
